<template >
  <section style="overflow-y: hidden;height: 100%;">
    <el-form :inline="true"
      style="height: 45px;">
      <el-form-item>
        <el-button type="primary"
          @click="handleAdd"
          size="small">{{constant.TIP.ADD}}
          <i class="el-icon-plus el-icon--right"></i>
        </el-button>
      </el-form-item>
    </el-form>
    <hr />
    <div class="d-table-wrapper">

      <c-el-table stripe
        ref="dTable"
        :isFold=false
        tree
        id="d-table"
        :data="departs"
        height="100%">
        <c-el-table-column label="名称"
          min-width="250"
          prop="name"
          tree
          show-overflow-tooltip>
        </c-el-table-column>
        <c-el-table-column label="父部门"
          min-width="150"
          prop="parentName"
          show-overflow-tooltip>
        </c-el-table-column>
        <c-el-table-column label="描述"
          min-width="200"
          prop="description"
          show-overflow-tooltip>
        </c-el-table-column>
        <c-el-table-column label="创建时间"
          min-width="150"
          prop="createDate"
          show-overflow-tooltip>
        </c-el-table-column>
        <c-el-table-column label="操作"
          min-width="200"
          prop="operation">
          <template slot-scope="scope">
            <el-tooltip class="item"
              effect="dark"
              :content="constant.TIP.EDIT"
              placement="top">
              <el-button size="small"
                type="primary"
                @click="handleEdit(scope.$index, scope.row)">
                <i class="el-icon-edit"></i>
              </el-button>
            </el-tooltip>
            <el-tooltip class="item"
              effect="dark"
              :content="constant.TIP.DELETE"
              placement="top">
              <el-button size="small"
                type="danger"
                @click="handleDel(scope.$index, scope.row)">
                <i class="el-icon-delete"></i>
              </el-button>
            </el-tooltip>
          </template>
        </c-el-table-column>
      </c-el-table>
    </div>

    <!--编辑部门界面-->
    <el-dialog title="编辑部门"
      :visible.sync="editFormVisible"
      v-if="editFormVisible"
      :close-on-click-modal="false">
      <el-form :model="editForm"
        label-width="80px"
        ref="editForm"
        :rules="editFormRules">
        <el-form-item label="名称"
          prop="name">
          <el-input v-model="editForm.name"
            auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="父级部门"
          prop="parentId">
          <el-cascader :options="options"
            :props="props"
            @change="handleChange"
            v-model="editForm.parentId"
            change-on-select
            :clearable="true"
            :show-all-levels="false"
            expand-trigger="hover">
          </el-cascader>
        </el-form-item>

        <el-form-item label="描述"
          prop="description">
          <el-input type="textarea"
            :autosize="{ minRows: 4, maxRows: 10}"
            placeholder="请添加相关描述"
            v-model="editForm.description"
            auto-complete="off"></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer"
        class="dialog-footer">
        <el-button @click.native="editFormVisible = false">取消</el-button>
        <el-button type="primary"
          @click.native="editSubmit"
          :loading="editLoading">提交</el-button>
      </div>
    </el-dialog>

    <!--新增部门界面-->
    <el-dialog title="新建部门"
      :visible.sync="addFormVisible"
      v-if="addFormVisible"
      :close-on-click-modal="false">
      <el-form :model="addForm"
        label-width="80px"
        ref="addForm"
        :rules="addFormRules">
        <el-form-item label="名称"
          prop="name">
          <el-input v-model="addForm.name"
            auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="父级部门"
          prop="parentId">
          <el-cascader :options="options"
            :props="props"
            @change="handleChange"
            v-model="addForm.parentId"
            change-on-select
            :clearable="true"
            :show-all-levels="false"
            expand-trigger="hover">
          </el-cascader>
        </el-form-item>

        <el-form-item label="描述"
          prop="description">
          <el-input v-model="addForm.description"
            type="textarea"
            :autosize="{ minRows: 4, maxRows: 10}"
            placeholder="请添加相关描述"
            auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer"
        class="dialog-footer">
        <el-button @click.native="addFormVisible = false">取消</el-button>
        <el-button type="primary"
          @click.native="addSubmit"
          :loading="addLoading">提交</el-button>
      </div>
    </el-dialog>

  </section>
</template>

<script>
export default {
  data() {
    // 新建部门时名称验证，只允许中文，英文，以及数字且名称唯一
    const validateName = (rule, value, callback) => {
      const re = new RegExp("^[a-zA-Z0-9\u4e00-\u9fa5]+$");
      console.log(value);

      if (!value || value.length == 0) {
        callback(new Error("请输入部门名称"));
      } else {
        if (re.test(value)) {
          if (value.length > 20) {
            callback(new Error("部门名称不得超过20个字符"));
          } else {
            const para = { name: "" };
            para.name = value;

            this.$http({
              url: "/api/depart/nameExist",
              method: "post",
              data: para
            }).then(res => {
              this.editLoading = false;
              // NProgress.done();
              if (res.data.flag) {
                callback(new Error("部门名称重复"));
              } else {
                callback();
              }
            });
          }
        } else {
          callback(new Error("只允许输入中文、字母、数字"));
        }
      }
    };
    // 编辑部门时名称验证，同上
    const validateName1 = (rule, value, callback) => {
      const re = new RegExp("^[a-zA-Z0-9\u4e00-\u9fa5]+$");

      if (!value || value.length == 0) {
        callback(new Error("请输入部门名称"));
      } else {
        if (re.test(value)) {
          if (value == this.departName) {
            callback();
          } else {
            if (value.length > 20) {
              callback(new Error("部门名称不得超过20个字符"));
            } else {
              const para = { name: "" };
              para.name = value;

              this.$http({
                url: "/api/depart/nameExist",
                method: "post",
                data: para
              }).then(res => {
                this.editLoading = false;
                // NProgress.done();
                if (res.data.flag) {
                  callback(new Error("部门名称重复"));
                } else {
                  callback();
                }
              });
            }
          }
        } else {
          callback(new Error("只允许输入中文、字母、数字"));
        }
      }
    };
    const validateParentId = (rule, value, callback) => {
      if (!value || value.length == 0) {
        callback();
      } else {
        const departs = this.$refs.dTable.treeData;
        for (const depart of departs) {
          if (depart.id == value[value.length - 1]) {
            if (depart._level > 5) {
              callback(new Error("部门层级不得超过6级"));
              break;
            } else {
              callback();
              break;
            }
          }
        }
      }
    };
    const validateParentId1 = (rule, value, callback) => {
      console.log(value);
      const parentId = value[value.length - 1];

      if (!value || value.length == 0 || parentId == 0) {
        callback();
      } else {
        // let departs = this.$refs.table.bodyData;
        const departs = this.$refs.dTable.treeData;
        for (const depart of departs) {
          if (depart.id == value[value.length - 1]) {
            if (depart._level > 5) {
              callback(new Error("部门层级不得超过6级"));
              break;
            } else {
              if (
                this.selectedDepart.childrenIds.length == 0 &&
                this.selectedDepart.id != parentId
              ) {
                callback();
                break;
              } else {
                if (
                  this.selectedDepart.childrenIds.indexOf(parentId) >= 0 ||
                  this.selectedDepart.id == parentId
                ) {
                  callback(new Error("父级部门不可选择该部门或该部门的子部门"));
                  break;
                } else {
                  callback();
                  break;
                }
              }
            }
          }
        }
      }
    };
    return {
      ccc: "ddd",

      departName: "",
      addFormRules: {
        name: [{ required: true, validator: validateName, trigger: "blur" }],
        parentId: [{ validator: validateParentId, trigger: "blur" }],
        description: [
          { max: 120, message: "长度不得超过120个字符", trigger: "blur" }
        ]
      },
      editFormRules: {
        name: [{ required: true, validator: validateName1, trigger: "blur" }],
        parentId: [{ validator: validateParentId1, trigger: "blur" }],
        description: [
          { max: 120, message: "长度不得超过120个字符", trigger: "blur" }
        ]
      },
      //   data:[],
      props: { value: "id", label: "name", children: "children" },
      departs: [],
      options: [],
      selectedOptions: [],
      selectedDepart: {},
      childrenIds: [],
      addFormVisible: false, // 新增界面是否显示
      addLoading: false,
      addForm: {},
      editFormVisible: false,
      editLoading: false,
      editForm: {},

      defaultProps: {
        children: "children",
        label: "name"
      },
      currentDepart: "",

      columns: [
        {
          label: "名称",
          prop: "name",
          minWidth: "300px",
          type: "template",
          template: "name"
        },
        {
          label: "父部门",
          prop: "parentName",
          minWidth: "100px",
          type: "template",
          template: "parentName"
        },
        {
          label: "描述",
          prop: "description",
          minWidth: "200px",
          type: "template",
          template: "description"
        },
        {
          label: "创建时间",
          prop: "createDate",
          minWidth: "150px"
        },
        {
          label: "操作",
          prop: "operation",
          minWidth: "200px",
          type: "template",
          template: "operation"
        }
      ]
    };
  },

  methods: {

    handleChange(value) {
      // console.log(value);
      this.currentDepart = value;
    },
    // 显示编辑界面
    handleEdit(index, row) {
      this.editFormVisible = true;
      this.selectedDepart = row;
      this.departName = row.name;
      this.editForm = Object.assign({}, row);
    },

    // 显示新增界面
    handleAdd: function () {
      this.addForm = {};
      this.addFormVisible = true;
    },
    addSubmit: function () {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          this.addLoading = true;
          // NProgress.start();

          const para = Object.assign({}, this.addForm);
          console.log(this.addForm.parentId);
          if (this.addForm.parentId && this.addForm.parentId.length > 0) {
            para.parentId = para.parentId[para.parentId.length - 1];
          } else {
            para.parentId = 0;
          }

          this.$http({
            method: "post",
            url: "api/depart/add",
            data: para
          }).then(res => {
            this.addLoading = false;
            // NProgress.done();
            if (res.data.flag) {
              this.$message({
                message: res.data.msg,
                type: "success"
              });
            } else {
              this.$message({
                message: res.data.msg,
                type: "error"
              });
            }
            this.$refs.addForm.resetFields();
            this.addFormVisible = false;
            this.getDeparts();
          });
        }
      });
    },
    editSubmit: function () {
      this.$refs.editForm.validate(valid => {
        if (valid) {
          this.editLoading = true;
          // NProgress.start();
          const para = Object.assign({}, this.editForm);
          if (this.editForm.parentId && this.editForm.parentId.length > 0) {
            para.parentId = para.parentId[para.parentId.length - 1];
          } else {
            para.parentId = 0;
          }
          this.$http({
            url: "/api/depart/update",
            method: "post",
            data: para
          }).then(res => {
            this.editLoading = false;
            // NProgress.done();
            if (res.data.flag) {
              this.$message({
                message: "编辑成功",
                type: "success"
              });
            } else {
              this.$message({
                message: "编辑失败",
                type: "error"
              });
            }

            this.$refs.editForm.resetFields();
            this.editFormVisible = false;
            this.getDeparts();
          });
        }
      });
    },
    handleDel(index, row) {
      if (row.id == 1) {
        this.$message({
          message: "根部门不允许删除",
          type: "error"
        });
      } else {
        this.$confirm("删除组织会将该组织下的人员一并删除，确认删除吗?", "提示", {
          type: "warning",
          closeOnClickModal: false
        })
          .then(() => {
            const para = { id: "" };
            para.id = row.id;
            this.$http({
              method: "post",
              url: "api/depart/delete",
              data: para
            }).then(res => {
              if (res.data.flag) {
                this.$message({
                  message: "删除成功",
                  type: "success"
                });
              } else {
                this.$message({
                  message: res.data.msg,
                  type: "error"
                });
              }
              this.getDeparts();
            });
          })
          .catch(() => { });
      }
    },
    getDeparts() {
      const _this = this;
      _this.$http
        .post("api/depart/list")
        .then(function (response) {
          _this.departs = response.data.depart;
          _this.options = response.data.depart;
        })
        .catch(function (error) {
          console.log(error);
        });
    }
  },
  mounted() {
    this.getDeparts();
  }
};
</script>

<style >
.d-table-wrapper {
  /*overflow: auto!important;
  height: auto !important;*/
  height: 80%;
  height: calc(100%-60px);
  height: -moz-calc(100% - 60px);
  height: -webkit-calc(100% - 60px);
}

.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inherit;
}
</style>